.notification {
  padding: 16px;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 8px;
}

.notification h2 {
  font-size: 20px;
  font-weight: bold;
  margin-top: 4px;
  margin-bottom: 16px;
}

.notification-preferences {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.notification-error {
  color: red;
  font-size: 1rem;
  margin-top: 10px;
}

.notification ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.notification ul li {
  margin: 4px 0;
}