.dashboard-container {
  text-align: center;
  padding: 20px;
}

.dashboard-container h1 {
  color: #333;
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.dashboard-container p {
  color: #555;
  font-size: 1.2rem;
  margin: 10px 0;
}

.dashboard-data-container {
  display: flex;
  flex-direction: column;
  gap: 24px; /* Add spacing between sections */
}

.market-data-container {
  display: flex; /* Use flexbox to align sections horizontally */
  flex-direction: row; /* Ensure sections are placed side by side */
  justify-content: space-between; /* Add space between left and right sections */
  align-items: flex-start; /* Align items at the top */
  gap: 16px; /* Add spacing between sections */
  width: 100%; /* Ensure the content takes full width */
}

.market-index,
.holdings-section {
  flex: 1; /* Make both sections take equal width */
  padding: 16px;
  border: 1px solid #ddd; /* Optional: Add a border for clarity */
  border-radius: 8px; /* Optional: Add rounded corners */
  background-color: #f9f9f9; /* Optional: Add a background color */
  box-sizing: border-box; /* Ensure padding is included in the width */
}

.market-index {
  margin-right: 8px; /* Add spacing to the right of the Market Index section */
}

.holdings-section {
  margin-left: 8px; /* Add spacing to the left of the Holdings section */
}

.market-index-items {
  display: flex; /* Display items horizontally */
  justify-content: center; /* Center items horizontally */
  align-items: center; /* Center items vertically */
  gap: 20px; /* Add spacing between items */
}

.market-item {
  margin-bottom: 20px;
}

.market-item h3 {
  margin: 0;
  font-size: 1.2em;
}

.market-item p {
  margin: 5px 0;
}

.change {
  font-weight: bold;
}

.change.positive {
  color: green; /* Green for positive change */
}

.change.negative {
  color: red; /* Red for negative change */
}